  findDuplicates(arr)  {
      let out;
      let result = []
      out = Array.from(new Set(arr.map(({city}) => city)))
      for( let i=0; i<out.length; i++ ) {
        result.push({city:out[i]})
      }
      return result;
    }
  findDuplicatescity(arr)  {
      let out;
      let result = []
      out = Array.from(new Set(arr.map(({city}) => city)))
      for( let i=0; i<out.length; i++ ) {
        result.push({city:out[i]})
      }
      return result;
    }
