  async seeProduct(mtp_id) {
    console.log(mtp_id);
    const modal = await this.modalController.create({
      component: ModelPage,
      cssClass: 'modalclass',
      componentProps: {
        mtp_id,
        "category": "seeproduct"
      },
      initialBreakpoint: 0.5,
      breakpoints: [0.25, 0.5, 1]
    });
    return await modal.present();
  }

  async seedcrProduct(dcr_id) {
    console.log(dcr_id);
    const modal = await this.modalController.create({
      component: ModelPage,
      cssClass: 'modalclass',
      componentProps: {
        dcr_id,
        "category": "seedcrproduct"
      },
      initialBreakpoint: 0.7,
      breakpoints: [0.25, 0.5, 1]
    });
    return await modal.present();
  }

